.cases {
	position: relative;
	padding: 15vh 30px 310px 50px;
	padding-top: calc(100px + (180 - 100)*((100vh - 750px)/(960 - 750)));
	height: 100%;
	max-height: 100%;
	width: 595px;
	@include css-lock(14,20,1024,1440);
	&__content {
		max-width: 402px; }
	&__title {
		margin-bottom: 3vh;
		max-width: 275px; }
	&__slider {
		position: absolute;
		left: 364px;
		bottom: 0;
		width: calc(100vw - ((100vw - 1440px) / 2 + 364px) ); }
	&__slider-wrap {
		padding-top: 80px;
		overflow: hidden; } }
.slider {
	width: 600px;
	font-size: 0;
	.slick-list {
		overflow: visible; }
	&__prev,
	&__next {
		position: absolute;
		top: -80px;
		width: 75px;
		height: 80px;
		text-align: center;
		background-color: red;
		.icon {
			position: absolute;
			top: 50%;
			left: 50%;
			margin: -9px 0 0 -5px;
			font-size: 18px; } }
	&__prev {
		left: 155px;
		color: $white;
		background-color: $black; }
	&__next {
		left: 230px;
		color: $black;
		background-color: $white; }
