@media (max-width: 320px) { .project_mob:not(:first-child) { margin-top: 5px; } }

@media (min-width: 1024px) { .project_mob:not(:first-child) { margin-top: 10px; } }

.project__img { width: 230px; height: 300px; vertical-align: top; background-position: 50% 50%; background-size: cover; display: table-cell; height: 300px; height: calc(150px + (300 - 150) * ((100vw - 1024px) / (1440 - 1024))); }

@media (max-width: 1024px) { .project__img { height: 150px; } }

@media (min-width: 1440px) { .project__img { height: 300px; } }

.project_mob .project__img { width: 40%; height: 390px; height: calc(195px + (390 - 195) * ((100vw - 320px) / (1024 - 320))); }

@media (max-width: 320px) { .project_mob .project__img { height: 195px; } }

@media (min-width: 1024px) { .project_mob .project__img { height: 390px; } }

.project__content { margin-bottom: 40px; padding: 20px 25px 45px; color: rgba(255, 255, 255, 0.5); letter-spacing: -0.02em; font-size: 16px; text-align: center; line-height: 1.625; vertical-align: middle; display: table-cell; }

.project_mob .project__content { font-size: 26px; font-size: calc(13px + (26 - 13) * ((100vw - 320px) / (1024 - 320))); line-height: 1.38; }

@media (max-width: 320px) { .project_mob .project__content { font-size: 13px; } }

@media (min-width: 1024px) { .project_mob .project__content { font-size: 26px; } }

.project__title { margin-bottom: 12px; font-family: "FuturaFuturis", Open Sans, sans-serif; font-size: 20px; font-weight: 500; line-height: 1.5; color: #fff; letter-spacing: 0; text-transform: uppercase; }

.project_mob .project__title { font-size: 40px; font-size: calc(13px + (40 - 13) * ((100vw - 320px) / (1024 - 320))); line-height: 1.333; }

@media (max-width: 320px) { .project_mob .project__title { font-size: 13px; } }

@media (min-width: 1024px) { .project_mob .project__title { font-size: 40px; } }
